import { h, Component } from "preact";
import ActionButton from "./ActionButton";
import Style from "../../styles/Style";
import axios from 'axios';
import Cookies from 'js-cookie'
import _ from 'lodash';
import Message from "./Message";
import { List } from 'immutable';
window.io = require('socket.io-client');
import Echo from 'laravel-echo';
import { Scrollbars } from 'preact-custom-scrollbars';
import classNames from "classnames";
import {MessageRecord} from "../../data/MessageRecord";
import Writing from "./Writing";

export default class App extends Component {

    static defaultProps = {
        embed: false,
        title: 'Chat',
        logo: 'data:image/png;base64,',
        botImage: 'data:image/png;base64,',
    };

    state = {
        open: this.props.open,
        soundNotifications: true,
        loadBtn: true,
        fullScreen: false,
        messages: List(),
        textEnabled: true,
        input: "",
        typingMessage: null,
        botTyping: false,
        newMessage: 0,
        sizeEnough: true,
    };

    constructor(props) {
        super(...arguments);

        this.toggleOpen = this.toggleOpen.bind(this);
        this.toggleNotifications = this.toggleNotifications.bind(this);
        this.toggleFullScreen = this.toggleFullScreen.bind(this);

        this.collectAndSendMessage = this.collectAndSendMessage.bind(this);
        this.sendMessage = this.sendMessage.bind(this);
        this.scrollBottom = this.scrollBottom.bind(this);

        this.client = axios.create({
            baseURL: this.props.baseURL,
            headers: {
                Authorization: "Bearer " + Cookies.get(this.getSessionName('token')),
                ClientOnly: '1'
            }
        });
        this.webhook = axios.create({
            headers: {
                ClientOnly: '1'
            }
        });

        window.addEventListener('message', (event) => {
            if(event.data === 'sizeEnough') {
                this.updateSizeEnough(true)
            }
            if(event.data === 'sizeIsNotEnough') {
                this.updateSizeEnough(false)
            }
        })
    }

    updateSizeEnough(newValue) {
        if(this.state.sizeEnough !== newValue) {
            this.setState({
                sizeEnough: newValue
            }, () => this.updateSize())
        }
    }

    get isFullScreen() {
        return this.state.fullScreen || !this.state.sizeEnough
    }

    scrollBottom() {
        if(this.state.open) {
            setTimeout(() => {
                this.setState({newMessages: 0}, () => this.list.scrollToBottom());
            })
        }
    }

    isScrolled() {
        return this.list.getScrollTop() + 60 < this.list.getScrollHeight() - this.list.getClientHeight();
    }

    get textTypeEnabled() {
        let lastMessage = this.state.messages.last();
        return !(lastMessage && lastMessage.buttons)
            && !this.state.typingMessage && this.state.textEnabled
    }

    componentDidMount() {
        this.registerSession();
    }

    collectAndSendMessage() {
        let message = this.state.input;
        if(message) {
            this.setState({input: ""});
            this.sendMessage(message)
        }
    }

    sendMessage(message) {
        return this.setState({textEnabled: false}, () =>  {
            this.webhook.post('', {
                message
            }).then(() => this.setState({textEnabled: true}))
        });
    }

    getSessionName(key) {
        return [
            key,
            this.props.id,
        ].join('|')
    }



    registerSession() {
        this.client.post('/session/create', {
            'url': this.props.currentURL,
            'agentId': this.props.id
        }).then(({data}) => {
            this.authorize(data.api_token, data.webhook);
            this.setState({
                messages: List(data.messages.map((message) => new MessageRecord(message)))
            }, () => {
                this.scrollBottom()
            })
        })
    }

    authorize(token, webhook) {
        Cookies.set(this.getSessionName('token'), token);
        this.client.defaults.headers.common['Authorization'] = "Bearer " + token;
        this.webhook.defaults.headers.common['Authorization'] = "Bearer " + token;
        this.webhook.defaults.baseURL = webhook;


        const EchoServer = new Echo({
            broadcaster: 'socket.io',
            host: this.props.baseURL,
            auth: {
                headers: {
                    Authorization: "Bearer " + token,
                    ClientOnly: '1'
                },
            },
        });

        this.channel = EchoServer.private('Session.' + token)

        this.channel.listen('ClientNewMessage', (e) => {
            const wasScrolled = this.isScrolled();
            let messageRecord = new MessageRecord(e.message);
            this.setState(({messages}) => ({
                messages: messages.push(messageRecord),
                botTyping: false,
            }), () => {
                if(!wasScrolled || messageRecord.getIsOutcome()) {
                    this.scrollBottom()
                } else {
                    this.setState(({newMessages}) => ({newMessages: newMessages+1}))
                }
            });

                // // this.messageAlert();
                // this.props.onMessage && this.props.onMessage(e.message);


        })
            .listen('BotTyping', () => {
                const wasScrolled = this.isScrolled();
                this.setState({botTyping: true}, () => {
                    if(!wasScrolled) {
                        this.scrollBottom()
                    }
                }) ;
            })
            .listenForWhisper('answering', () => {
                    if (!this.state.typing) {
                        let cb = this.isScrolled() ? () => {
                            this.scrollBottom()
                        } : () => {
                        };
                        this.setState({typing: true}, cb)
                    }
                }
            )
            .listenForWhisper('stop-answering', () => {
                if (this.state.typing) {
                    this.setState({typing: false})
                }
            })
    }


    open() {
    }

    postMessage(event, params = {}) {
        window.parent.postMessage(JSON.stringify({event, ...params}), '*');
    }

    updateSize() {
        if(this.state.open && this.isFullScreen) {
            this.postMessage('full-screen');
        } else {
            this.postMessage('resize');
        }
    }

    toggleOpen() {
        this.setState(({open}) => ({open: !open}), () => {
            this.state.open && this.scrollBottom();
            this.updateSize();
        });
    }

    toggleNotifications() {
        this.setState(({soundNotifications}) => ({soundNotifications: !soundNotifications}));
    }

    toggleFullScreen() {
        this.setState(({fullScreen}) => ({fullScreen: !fullScreen}), () => {
            this.updateSize();
        });
    }

    renderButton() {
        return (<div onClick={this.toggleOpen} className={'speech-bubble-wrapper'}>
            <i className={'icon-button speech-bubble-icon'}/>
        </div>)
    }

    renderWidget() {
        const {
            logo,
            title,
            botImage
        } = this.props;

        const state = this.state;
        const messages = state.messages.toArray();

        let lastMessage;

        if(state.botTyping) {
            lastMessage = new MessageRecord({type: 4, message: <Writing/>})
        } else {
            lastMessage = messages.pop();
        }

        return (
            <div className={classNames("window-wrapper", {"fullscreen": this.isFullScreen})}>
                <div className="window-header">
                    <div className="name">
                        <img src={logo} alt={title} className={'logo-img'}/>
                        <span className={'title'}>{title}</span>
                    </div>
                    <div className="configs">
                        <ActionButton
                            icon={state.soundNotifications ? "icon-bell" : "icon-bell-slash"}
                            onClick={this.toggleNotifications}
                        />
                        {
                            this.state.sizeEnough && <ActionButton
                                icon={"icon-window-minimize"}
                                onClick={this.toggleFullScreen}
                            />

                        }
                        <ActionButton
                            icon={"icon-times-solid"}
                            onClick={this.toggleOpen}
                        />
                    </div>
                </div>
                <div className="window-body">
                    <div className={'relative'}>
                        <Scrollbars
                            className="messages-list has-files"
                            ref={(el) => this.list = el}
                            renderThumbVertical={props => <div {...props} className="scroll-thumb-vertical"/>}
                            renderTrackVertical={props => <div {...props} className="scroll-track-vertical"/>}
                            renderView={props => <div {...props} className="scroll-view"/>}
                        >
                            {
                                messages.map((message) => <Message
                                    key={message.id}
                                    message={message}
                                    image={botImage}
                                    showImage={true}
                                />)
                            }
                            {lastMessage && <Message
                                key={lastMessage.id}
                                allowButton
                                sendMessage={this.sendMessage}
                                message={lastMessage}
                                image={botImage}
                                showImage={true}
                            />}
                            {
                                this.state.typingMessage && <div>...</div>
                            }
                        </Scrollbars>
                        {
                            !!state.newMessages && <div className={'unread-messages'} onClick={this.scrollBottom}>
                                Unread messages ({state.newMessages})
                            </div>
                        }
                    </div>

                    <div className={'file-list'}>
                        <div className={'file-item'}><i className={'icon-file-image-solid'}/><span className={'file-name'}>file 1</span><i className={'delete-file icon-times-solid'}/></div>
                        <div className={'file-item'}><i className={'icon-file-image-solid'}/><span className={'file-name'}>file 2</span><i className={'delete-file icon-times-solid'}/></div>
                    </div>

                    <div className={`type-message ${!this.textTypeEnabled && 'disabled'} ${state.loadBtn && 'has-load-btn'}`}>
                        <textarea
                            disabled={!this.textTypeEnabled}
                            value={state.input}
                            onInput={(e) => {
                                if(this.enter) return;
                                const input = e.target.value;
                                this.channel.whisper('typing', {
                                    message: input
                                });
                                this.setState({input})
                            }}
                            placeholder={'Start typing here...'}
                            onKeyDown={(e) => {
                                this.enter = false;
                                if (e.keyCode === 13 && !e.shiftKey) {
                                    this.enter = true;
                                    e.stopPropagation();
                                    this.collectAndSendMessage();
                                }
                            }
                            }
                        />
                            {state.loadBtn &&
                            <div className={'load-btn'}>
                                <label htmlFor="file"><i className={'icon-paperclip-solid'}/></label>
                                <input hidden name="file" id="file" type="file"/>
                            </div>
                            }
                            <a className={'send-btn'} onClick={this.collectAndSendMessage}><i className={'icon-telegram-plane'}/></a>
                    </div>
                </div>
                <div className="window-footer">
                    <div className="powered">Powered by promptchat</div>
                </div>
            </div>
        )
    }

    render() {
        return <div>
            <Style />
            {this.state.open && this.renderWidget()}
            <div />
            {this.renderButton()}
        </div>
    }
}



