import {h, Component} from "preact";
import classNames  from "classnames";
import _ from 'lodash';


export default class Message extends Component {

    shouldComponentUpdate(nextProps) {
        return this.props.message !== nextProps.message || this.props.allowButton !== nextProps.allowButton
    }

    get buttons() {
        return this.props.allowButton && this.props.message.buttons
    }



    render() {
        const message = this.props.message;

        return (
            <div className={classNames("message-block", {"income": message.getIsIncome(), "outcome": message.getIsOutcome()})}>
                {message.getIsIncome() && this.props.showImage &&
                    <div className="image-block">
                        <img className={'image'} src={this.props.image ? this.props.image : message.image} alt=""/>
                    </div>
                }
                <div className="messages">
                    <div className="message-item">
                        <div className="message">
                            <div>
                                {message.message}
                            </div>
                            {
                                message.images && <div>
                                    {
                                        message.images.map((img) => {
                                            return <img style={{height: 100}} src={img.data.url} key={img.id} alt=""/>
                                        })
                                    }

                                </div>
                            }

                            {
                                this.buttons && <div>
                                    {
                                        this.buttons.map(({text}) =>  <button className={'btn'} onClick={() => this.props.sendMessage(text)}>{text}</button>)
                                    }
                                </div>
                            }
                        </div>
                        <div className="time">
                            {this.props.time}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}



