ar(--header-bg-color);\n  color: var(--header-color);\n  font-size: var(--header-font-size);\n  font-family: var(--header-font-family);\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 0 5px;\n  \n  .name {\n    display: flex;\n    align-items: center;\n    \n    .logo-img {\n      width: 40px;\n      height: 40px;\n    }\n    \n    .title {\n      margin: 0 5px;\n    }\n  }\n  \n  .action-btn {\n    font-size: 14px;\n    cursor: pointer;\n    margin: 6px;\n    display: inline-block;\n  }\n}\n\n.window-body {\n  height: var(--window-body-height);\n  background-color: var(--window-body-bg-color);\n  color: var(--window-body-color);\n  position: relative;\n\n  .messages-list {\n    height: 440px!important;\n    overflow: auto;\n    position: relative;\n    \n    &.has-files {\n      height: 420px!important;\n    }\n  }\n  \n  .unread-messages {\n    position: absolute;\n    left: 0;\n    right: 0;\n    bottom: 5px;\n    margin: 0 auto;\n    width: 150px;\n    text-align: center;\n    background: white;\n    color: black;\n    padding: 5px;\n    border-radius: 10px;\n    animation-duration: .5s;\n    animation-name: fly-in;\n    cursor: pointer;\n    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);\n  }\n  \n  .file-list {\n    padding: 0 10px;\n    font-size: 13px;\n    height: 20px;\n    display: flex;\n    \n    .file-item {\n      display: inline-flex;\n      align-items: center;\n      margin-right: 8px;\n  \n      .file-name {\n        margin: 0 3px;\n      }\n      \n      .delete-file {\n        font-size: 8px;\n        cursor: pointer;\n      }\n    }\n  }\n  \n  .type-message {\n    height: 50px;\n    position: relative;\n    padding: 5px;\n    \n    &.disabled {\n      opacity: 0.5;\n      \n      .send-btn {\n        cursor: default;\n      }\n    }\n    \n    &.has-load-btn {\n      textarea {\n        width: calc(100% - 90px);\n        padding: 10px 80px 10px 10px;\n      }\n    }\n    \n    textarea {\n      color: var(--textarea-color);\n      background-color: var(--textarea-bg-color);\n      font-size: var(--textarea-font-size);\n      font-family: var(--textarea-font-family);\n      width: calc(100% - 60px);\n      height: 30px;\n      border: none;\n      padding: 10px 50px 10px 10px;\n      position: relative;\n      outline: none;\n      overflow: hidden;\n      resize: none;\n      border-radius: 10px;\n      \n      &::placeholder{\n        color: var(--textarea-placeholedr-color);\n        font-size: var(--textarea-font-size);\n        font-family: var(--textarea-font-family);\n      }\n    }\n  \n    .load-btn,\n    .send-btn {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      position: absolute;\n      top: 10px;\n      width: 40px;\n      height: 40px;\n    }\n    \n    .load-btn {\n      right: 50px;\n      font-size: 18px;\n      \n      label {\n        cursor: pointer;\n      }\n    }\n    \n    .send-btn {\n      color: var(--send-btn-color);\n      background-color: var(--send-btn-bg-color);\n      outline: none;\n      right: 10px;\n      border-radius: 10px;\n      cursor: pointer;\n      font-size: 20px;\n    }\n  }\n}\n\n.window-footer {\n  height: var(--window-footer-height);\n  background-color: var(--footer-bg-color);\n  color: var(--footer-color);\n  font-size: var(--footer-font-size);\n  font-family: var(--footer-font-family);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 0 10px;\n}\n\n.message-block {\n  display: flex;\n  margin: 10px 0;\n  \n  .image-block {\n    width: 44px;\n    height: 40px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    \n    .image {\n      width: 34px;\n      height: 34px;\n      object-fit: cover;\n      border-radius: 50%;\n    }\n  }\n  \n  .messages {\n    width: 100%;\n    \n    .message-item {\n      display: flex;\n      align-items: center;\n      animation-duration: .5s;\n      animation-name: fly-in;\n      \n      .message {\n        margin: 0 10px;\n        padding: 12px;\n        border-radius: 10px;\n        min-height: 16px;\n        min-width: 20px;\n        word-break: break-word;\n        \n        .btn {\n          border-radius: 10px;\n          padding: 7px 10px;\n          margin: 5px 5px 0 0;\n          cursor: pointer;\n          display: inline-block;\n          border: none;\n          box-shadow: none;\n        }\n      }\n    }\n  }\n  \n  &.income {\n    .image-block {\n      background: var(--message-income-bg-color);\n      border-radius: 0 50% 50% 0;\n    }\n    \n    .message {\n      background-color: var(--message-income-bg-color);\n      color: var(--message-income-color);\n      font-size: var(--message-income-font-size);\n      font-family: var(--message-income-font-family);\n      \n      .btn {\n        background: var(--message-income-btn-bg-color);\n        color: var(--message-income-btn-color);\n        font-size: var(--message-income-btn-font-size);\n        font-family: var(--message-income-btn-font-family);\n      }\n    }\n  }\n  \n  &.outcome {\n    flex-direction: row-reverse;\n    \n    .message-item {\n      justify-content: flex-end;\n      margin-left: 40px;\n      \n      .message {\n        background-color: var(--message-outcome-bg-color);\n        color: var(--message-outcome-color);\n        font-size: var(--message-outcome-font-size);\n        font-family: var(--message-outcome-font-family);\n      }\n    }\n  }\n}\n\n.scroll-thumb-vertical {\n  background: var(--window-body-bg-color);\n  filter: invert(1);\n  opacity: 0.5;\n}\n\n.scroll-track-vertical {\n  right: 2px;\n  bottom: 2px;\n  top: 2px;\n  border-radius: 3px;\n}\n\n.rtl {\n  direction: rtl;\n  \n  .scroll-view {\n    margin-right: 0!important;\n    margin-left: -15px;\n  }\n  \n  .scroll-track-vertical {\n    right: unset;\n    left: 2px;\n  }\n  \n  .message-block {\n    &.income .image-block {\n      border-radius: 50% 0 0 50%;\n    }\n    \n    &.outcome .message-item {\n      margin-left: 0;\n      margin-right: 40px;\n    }\n  }\n  \n  .window-body .type-message {\n    textarea {\n      padding: 10px 10px 10px 50px;\n    }\n    \n    .send-btn {\n      right: unset;\n      left: 10px;\n    }\n  }\n}\n\n.speech-bubble-wrapper {\n  width: var(--bubble-size);\n  height: var(--bubble-size);\n  border: 1px solid var(--bubble-color);\n  background: var(--bubble-bg-color);\n  border-radius: 50%;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  float: var(--window-position);\n  margin: 20px;\n  animation: play 1.5s ease infinite;\n  z-index: 1;\n\n  .speech-bubble-icon {\n    font-size: calc(var(--bubble-size) / 2);\n    color: var(--bubble-color)\n  }\n}\n\n.writing {\n  position: relative;\n  text-align: center;\n  margin: 0 auto;\n  width: 60px;\n  \n  .dot {\n    display: inline-block;\n    width: 10px;\n    height: 10px;\n    border-radius: 50%;\n    margin-right: 10px;\n    background: #fff;\n    \n    &:first-of-type {\n      position: absolute;\n      top: 2px;\n      animation: dot-shuttle 2.5s infinite ease;\n    }\n  }\n}\n\n/* Animations */\n@keyframes fly-in {\n  0% {\n    transform: scale(0) translateY(100%);\n    opacity: 0;\n  }\n  100% {\n    transform: scale(1) translateY(0);\n    opacity: 1;\n  }\n}\n\n@keyframes play {\n  0% {\n    transform: scale(1);\n  }\n  \n  15% {\n    box-shadow: 0 0 0 4px var(--bubble-rgb-color);\n  }\n  \n  25% {\n    box-shadow: 0 0 0 4px var(--bubble-rgb-color), 0 0 0 8px var(--bubble-rgb-color);\n  }\n  \n  30% {\n    transform: scale(1.2);\n  }\n  \n  50% {\n    box-shadow: 0 0 0 4px var(--bubble-rgb-color), 0 0 0 8px var(--bubble-rgb-color), 0 0 0 12px var(--bubble-rgb-color);\n  }\n  \n  80% {\n    transform: scale(1);\n  }\n}\n\n@keyframes dot-shuttle {\n  0%, 25%, 50%, 75%, 100% {\n    width: 15px;\n    height: 15px;\n    top: -2px;\n    left: -2px;\n  }\n  \n  5%, 30%, 55%, 80% {\n    width: 10px;\n    height: 10px;\n    top: 2px;\n  }\n  \n  0% {\n    transform: translateX(0);\n    \n  }\n  25% {\n    transform: translateX(20px);\n  }\n  50% {\n    transform: translateX(40px);\n  }\n  75% {\n    transform: translateX(20px);\n  }\n  100% {\n    transform: translateX(0);\n  }\n}\n"],"sourceRoot":""}]);

